.app {
  max-width: 1280px;
  height: 2000px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.button {
  width: 230px;
  height: 50px;
  border: 2px solid #E9E9E9;
  background-color: transparent;
  border-radius: 10px;
  color: #E9E9E9;
  font-size: 18px;
  margin-top: 80px;
}

.button:last-of-type {
  margin-top: 20px;
}

@media screen and (max-width: 560px) {
  .app {
    padding: 20px 10px 20px 10px;
  }
}